import React, { Component } from 'react';
import { ColorPicker } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect} from 'react-redux';
import { fromJS, is } from 'immutable';
import { setHeaderBg} from '../../../../store/defaultConfigs/action'

class XcolorPicker extends Component {
	state = {
		color: ''
	}

	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}

	onChange = async (n) => {
		if (!n) n = '#fff'
		await this.props.setHeaderBg(n)
	}

	render() {
		const { defaultOptions} = this.props.configs

		return (
			<div id="x-time-picker">
				<h3>ColorPicker 颜色选择器</h3>
				<div className="mt-20 mb-20">
					<ColorPicker value={this.state.color} onChange={this.onChange}></ColorPicker>
				</div>
				<Codemirror value={
					`<ColorPicker value={this.state.color}></ColorPicker>`
				} options={defaultOptions} />

			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {
	setHeaderBg
})(XcolorPicker)